import React, { useEffect, useState } from 'react';
import styles from './index.module.scss';
import { ReactMarkdown } from 'react-markdown/lib/react-markdown';
import DocumentSettings from '@utils/seo';
import PageLoader from '@components/Loader/index';
import { connect } from 'react-redux';
import { getShort } from '@modules/cms';
import { withRouter } from 'react-router-dom';
import { Button } from 'react-bootstrap';
import PostSocials from '@components/PostSocials';


const mapStateToProps = (state: any) => {
    return {
        cms: state.cms,
        window: state.window,
    }
}

const mapActionsToProps = {
    getShort
}

const ShortsVideoScreen: React.FunctionComponent<any> = (props) => {
    const [loaded, setLoaded] = useState(false)
    const history = props?.history;
    const aspectRatio = 16 / 9;

    const sWidth = props?.window?.sizes?.width;
    const sHeight = props?.window?.sizes?.height;

    const shortEmbedWidth: number = sWidth > 992
        ? 540
        : (sWidth > 576)
            ? (sWidth >= 768)
                ? (sWidth < 992)
                    ? 700
                    : 560
                : 540
            : (sWidth - 20)

    const shortEmbedHeight: number = shortEmbedWidth * aspectRatio;
    useEffect(() => {
        props.getShort({
            path: props?.match?.params?.id,
        }).then((res: any) => {
            console.log(res)
        }).catch((err: any) => {
            console.log(err)
        })
        setLoaded(true)
    }, [])
    const short = props?.cms?.shorts?.[props?.match?.params?.id]?.attributes;

    const youtubeId = short?.youtubeEmbedUrl.split("https://www.youtube.com/embed/")[1];
    const watchUrl = "https://www.youtube.com/shorts/" + youtubeId;

    const seo = {
        title: short?.title,
        description: short?.content,
        keywords: short?.tags,
        author: "Can Gökçeaslan",
        locale: "tr",
        image: "https://www.cangokceaslan.com/favicon.ico",
        siteName: "https://www.cangokceaslan.com",
        video: watchUrl,
        type: "video"
    }

    const keywords = short?.tags?.split(",").map((tag: any) => {
        return tag
    }) || [];

    if (props?.cms?.shorts?.[props?.match?.params?.id] === null) {
        history.push("/")
        return <PageLoader loaded={loaded} />
    }

    if (!short?.title) {
        return <PageLoader loaded={loaded} />
    }
    return (
        <div className="container">
            <DocumentSettings docData={seo} />
            <div className={styles.shortsVideoScreen}>
                <article>
                    <figure>
                        <iframe
                            className={styles.video}
                            width={shortEmbedWidth + "px"}
                            height={shortEmbedHeight + "px"}
                            src={short?.youtubeEmbedUrl}
                            title="Library ve Framework nedir? Ne gibi farkları vardır? #shorts #yazılım #yazilim"
                            frameBorder="0"
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                            allowFullScreen
                        />
                    </figure>
                    <div role="heading" className={styles.title}>
                        {short?.title}
                    </div>
                    <div className={styles.tags}>
                        {keywords.map((tag: any, index: number) => {
                            if (index === keywords.length - 1) {
                                return "#" + tag.trim()
                            }
                            return "#" + tag.trim() + " "
                        })}
                    </div>
                    <div role="contentinfo" className={styles.text}>
                        <ReactMarkdown>
                            {short?.content}
                        </ReactMarkdown>
                    </div>
                </article>
            </div>
            <PostSocials />
        </div>
    )
}

export default connect(mapStateToProps, mapActionsToProps)(withRouter(ShortsVideoScreen));